.App {
  text-align: center;
}

.board, .case {
  border: 1px solid black;
}

.board{
  display: grid;
  grid-template-columns: repeat(3, 100px);
  width: 300px;
}

.case {
  font-size: 50px;
  width: 100px;
  height: 100px;
  text-align: center;
}

.red-border {
  border: 1px solid red;
}